@import "./Shared.scss";

.App {
    text-align: left;
    justify-content: center;
    max-width: 900px;
    margin: auto;
    padding: 1.2rem;
    .App-header {
      padding: 10px;
      h1 {
        font-family: 'Powerpuff';
      }
    }
    h3.Error-message {
      color: $errorMessageColor;
    }
  }

  @font-face {
    font-family: 'Powerpuff';
    src: url('./fonts/Powerpuff.eot');
    src: url('./fonts/Powerpuff.eot?#iefix') format('embedded-opentype'),
        url('./fonts/Powerpuff.woff2') format('woff2'),
        url('./fonts/Powerpuff.woff') format('woff'),
        url('./fonts/Powerpuff.ttf') format('truetype'),
        url('./fonts/Powerpuff.svg#Powerpuff') format('svg');
    font-weight: normal;
    font-style: normal;
}
  